import { useState } from 'react';
import {Store } from 'react-notifications-component';
import Validator from 'validator';
import { useHistory } from 'react-router-dom';
import Axios from 'axios';

export default function Register() {
  const history = useHistory();
  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [password1, setPassword1] = useState("");
  const [idNumber, setIdNumber] = useState("");
  const [address, setAddress] = useState("");

  const handleRegister = () => {
    if (name === "" || email==="" || password==="" || password1==="" || idNumber==="" || address==="") {
      Store.addNotification({
        title: "Wonderful!",
        message: "Please fill all fields",
        type: "warning",
        insert: "top",
        container: "top-right",
        animationIn: ["animate__animated", "animate__fadeIn"],
        animationOut: ["animate__animated", "animate__fadeOut"],
        dismiss: {
          duration: 5000,
          onScreen: true
        }
      });
    }
    if (email!=="" && !Validator.isEmail(email)) {
      Store.addNotification({
        title: "Wonderful!",
        message: "email is not valid!",
        type: "warning",
        insert: "top",
        container: "top-right",
        animationIn: ["animate__animated", "animate__fadeIn"],
        animationOut: ["animate__animated", "animate__fadeOut"],
        dismiss: {
          duration: 5000,
          onScreen: true
        }
      });
    }
    if (password.length < 6) {
      Store.addNotification({
        title: "Wonderful!",
        message: "Password must be at least 6 characters!",
        type: "warning",
        insert: "top",
        container: "top-right",
        animationIn: ["animate__animated", "animate__fadeIn"],
        animationOut: ["animate__animated", "animate__fadeOut"],
        dismiss: {
          duration: 5000,
          onScreen: true
        }
      });
    }
    if (password !== password1) {
      console.log("wowow")
      Store.addNotification({
        title: "Wonderful!",
        message: "password is not match!",
        type: "warning",
        insert: "top",
        container: "top-right",
        animationIn: ["animate__animated", "animate__fadeIn"],
        animationOut: ["animate__animated", "animate__fadeOut"],
        dismiss: {
          duration: 5000,
          onScreen: true
        }
      });
    }
    if (name !== "" && email !== "" && password !== "" && password1 !== "" && idNumber !== "" && address !== "" && password === password1 && Validator.isEmail(email) && password.length>=6) {
      Axios.post("/api/users/register", {name: name, email: email, password: password, idNumber: idNumber, address: address })
        .then((res) => {
          console.log("res data", res.data);
          history.push("/login");
        })
        .catch((err) => {
          console.log(err.response.data.msg);
          Store.addNotification({
            title: "Wonderful!",
            message: err.response.data.msg,
            type: "danger",
            insert: "top",
            container: "top-right",
            animationIn: ["animate__animated", "animate__fadeIn"],
            animationOut: ["animate__animated", "animate__fadeOut"],
            dismiss: {
              duration: 5000,
              onScreen: true
            }
          });
      })
    }
  }
  return (
    <div className='x-container2 x-auth-container'>
        <div className='x-font1'>
          Register
        </div>
      <div className=' = "x-center x-margin'>
          <div className='x-font2 x-margin'>Name</div>
          <div><input className="x-input1" type="text" onChange={(e)=>setName(e.target.value)}/></div>
          <div className='x-font2 x-margin'>Email</div>
          <div><input className="x-input1" type="email" onChange={(e)=>setEmail(e.target.value)}/></div>
          <div className='x-font2 x-margin'>Password</div>
          <div><input className="x-input1" type="password" onChange={(e)=>setPassword(e.target.value)} /></div>
          <div className='x-font2 x-margin'>Confirm Password</div>
          <div><input className="x-input1" type="password" onChange={(e)=>setPassword1(e.target.value)} /></div>
          <div className='x-font2 x-margin'>ID number</div>
          <div><input className="x-input1" type="number" onChange={(e)=>setIdNumber(e.target.value)} /></div>
          <div className='x-font2 x-margin'>Address</div>
          <div><input className="x-input1" type="text" onChange={(e)=>setAddress(e.target.value)} /></div>
          <div className = "x-center x-margin1">
            <button className='x-button1 x-font2' onClick = {handleRegister}>Register</button>
          </div>
        </div>
        </div>
  )
}